@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

html{
    box-sizing: border-box;
}
*, *:before, *::after{
    box-sizing: inherit;
}


html,
body,
p,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li,
button{
    padding: 0px;
    margin: 0px;
    outline: none;
    border: 0px;
}

a{
    text-decoration: none;
    outline: none;
    border: 0px;
    color: inherit;
    &:hover, &:link, &:active{
        text-decoration: none;
    }
}

ul, 
ol,
li{
    list-style-type: none ;
}

body {
    font-family: 'Roboto';
    background: #3E3E3E;
}

.App {
    max-width: 390px;
    min-height: 584px;
    border-radius: 20px;
    background: white;
    margin: 40px auto;
    padding: 20px;
}