.list-area{
    font-size: 20px;
    line-height: 23px;
    color: #FFFFFF;
}

.list-item {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 30px auto;
    &__content {
        flex-basis: 304px;
        padding: 30px;
        background: #000000;
        border-radius: 20px;
    }

    &__btn {
        width: 40px;
        height: 40px;
        cursor: pointer;
        background: url(./../images/delete-ico.svg) no-repeat center;
    }
}

.list-input{
    width: 100%;
    padding: 30px;
    background: rgba(0, 0, 0, 0.8);
    border-radius: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &__input{
        font-size: 20px;
        line-height: 23px;
        color: #FFFFFF;
        border: none;
        outline: none;
        background: transparent;
        &::placeholder{
            color: rgba(255, 255, 255, 0.349);
        }
    }

    &__btn{
        cursor: pointer;
        border: none;
        width: 35px;
        height: 35px;
        background: url(./../images/input-btn-ico.svg)no-repeat center;
    }
}


